<template>
  <div>
    <van-nav-bar title="首页" left-arrow @click-left="$router.go(-1)" />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in bannerList" :key="item.id">
        <img :src="$preImg + item.img" alt="" />
      </van-swipe-item>
    </van-swipe>
    <van-tabs v-model="active">
      <van-tab title="热门推荐">
        <van-card
          tag="hot"
          :price="item.price"
          :title="item.goodsname"
          :thumb="$preImg + item.img"
          v-for="item in goodsList[0].content"
          :key="item.id"
        >
          <template #footer>
            <van-button size="mini" @click="add(item.id)"
              >加入购物车</van-button
            >
          </template>
        </van-card>
      </van-tab>
      <van-tab title="发现商品">
        <van-card
          tag="hot"
          :price="item.price"
          :title="item.goodsname"
          :thumb="$preImg + item.img"
          v-for="item in goodsList[1].content"
          :key="item.id"
        >
          <template #footer>
            <van-button size="mini" @click="add(item.id)"
              >加入购物车</van-button
            >
          </template>
        </van-card>
      </van-tab>
      <van-tab title="所有商品">
        <van-card
          tag="hot"
          :price="item.price"
          :title="item.goodsname"
          :thumb="$preImg + item.img"
          v-for="item in goodsList[2].content"
          :key="item.id"
        >
          <template #footer>
            <van-button size="mini" @click="add(item.id)"
              >加入购物车</van-button
            >
          </template>
        </van-card>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { getBanner, getIndexGoods, addCart } from "../utils/request";
export default {
  data() {
    return {
      active: 0,
      bannerList: [],
      goodsList: [{ content: [] }, { content: [] }, { content: [] }],
    };
  },
  methods: {
    ...mapActions({
      requestCartList: "cartListActions",
    }),
    onClickLeft() {
      Toast("返回");
    },
    //获取轮播图
    requestBnnerList() {
      getBanner().then((res) => {
        this.bannerList = res.data.list;
      });
    },
    //获取商品
    requestIndexGoods() {
      getIndexGoods().then((res) => {
        this.goodsList = res.data.list;
      });
    },
    //添加购物车
    add(goodsid) {
      //接口需要的参数
      var data = {
        uid: this.user.uid,
        goodsid,
        num: 1,
      };
      // console.log(this.user);
      // console.log(data);
      //添加购物车请求
      addCart(data).then((res) => {
        this.$toast.success("添加购物车成功");
        this.requestCartList(this.user.uid);
      });
    },
  },
  computed: {
    //获取用户信息
    ...mapGetters({
      user: "user",
    }),
  },
  mounted() {
    this.requestBnnerList();
    this.requestIndexGoods();
  },
};
</script>

<style scoped>
.van-tab__pane {
  margin-bottom: 50px;
}
.my-swipe .van-swipe-item img {
  width: 100%;
  height: 100%;
}
.van-button {
  background-color: rgb(107, 207, 112);
  color: aliceblue;
  border-radius: 5px;
}
</style>


